
import { mapGetters,mapActions } from 'vuex'

export default {
    data() {
        return {
        }
    },

    created() {
        this.checkLogin({ path: '/login' })
        
        this.getNotebooks()
    },

    computed: {
      ...mapGetters(['notebooks'])
    },

    methods: {
        ...mapActions([
          'getNotebooks',
          'addNotebook',
          'updateNotebook',
          'deleteNotebook',
          'checkLogin',
          'getNotebooks'
        ]),
        onCreate() {
            this.$prompt('输入新笔记本标题', '创建笔记本', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                inputPattern: /^.{1,30}$/,
                inputErrorMessage: '标题不能为空，且不能超过30字符'
              }).then(({ value }) => {
                 this.addNotebook({ title: value })
              })
        },

        onEdit(notebook) {
            let title = ''
            this.$prompt('输入新的笔记本标题', '修改标题', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                inputValue: notebook.title,
                inputPattern: /^.{1,30}$/,
                inputErrorMessage: '标题不能为空，且不能超过30字符'
              }).then(({ value }) => {
                  this.updateNotebook({ notebookId: notebook.id,title: value})
              })
        },

        onDelete(notebook) {
            this.$confirm('此操作将删除该笔记本, 是否继续?', '删除笔记本', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                this.deleteNotebook({notebookId: notebook.id})
              })
        }
    }

}


